:root {
/* Font Family */
  --font-primary: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  --font-secondary: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  --font-hero: 'Signature Collection';
/* Font Size */
  --font-size-base: 1.25rem;
  --font-size-xs: 1rem;
  --font-size-sm: 1.125rem;
  --font-size-md: 1.25rem;
  --font-size-lg: 1.5rem;
  --font-size-xl: 2.25rem;
  --font-size-xxl: 4rem;
  --font-size-xxxl: 6rem;
  --font-size-hero-title: calc(48px + (64 - 24) * ((100vw - 320px) / (1600 - 320)));
/* Headings */
  --heading--font-family: var(--font-secondary);
/* Colors */
  --color-white: #fff;
  --color-dark-gray: #28303d;
  --color-gray: #39414d;
  --color-light-gray: #f0f0f0;
  --color-white-50: rgba(255,255,255,0.5);
  --color-white-90: rgba(255,255,255,0.9);
  --color-primary: var(--color-dark-gray);
}
.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}
/* normalize.css v8.0.1 | MIT License | github.com/necolas/normalize.css */
*,
*::before,
*::after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
  line-height: 1.15;
/* 1 */
  -webkit-text-size-adjust: 100%;
/* 2 */
}
body {
  margin: 0;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
hr {
  box-sizing: content-box;
/* 1 */
  height: 0;
/* 1 */
  overflow: visible;
/* 2 */
}
pre {
  font-family: monospace;
/* 1 */
  font-size: 1em;
/* 2 */
}
a {
  background-color: transparent;
  text-decoration-thickness: 1px;
}
abbr[title] {
  border-bottom: none;
/* 1 */
  text-decoration: underline;
/* 2 */
  text-decoration-style: dotted;
/* 2 */
}
b,
strong {
  font-weight: bolder;
}
code,
kbd,
samp {
  font-family: monospace;
/* 1 */
  font-size: 1em;
/* 2 */
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
img {
  border-style: none;
  vertical-align: top;
  max-width: 100%;
}
button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
/* 1 */
  font-size: 100%;
/* 1 */
  line-height: 1.15;
/* 1 */
  margin: 0;
/* 2 */
}
button,
input {
/* 1 */
  overflow: visible;
}
button,
select {
/* 1 */
  text-transform: none;
}
button,
[type=button],
[type=reset],
[type=submit] {
  appearance: button;
}
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  padding: 0.35em 0.75em 0.625em;
}
legend {
  box-sizing: border-box;
/* 1 */
  color: inherit;
/* 2 */
  display: table;
/* 1 */
  max-width: 100%;
/* 1 */
  padding: 0;
/* 3 */
  white-space: normal;
/* 1 */
}
progress {
  vertical-align: baseline;
}
textarea {
  overflow: auto;
}
[type=checkbox],
[type=radio] {
  box-sizing: border-box;
/* 1 */
  padding: 0;
/* 2 */
}
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}
[type=search] {
  appearance: textfield;
/* 1 */
  outline-offset: -2px;
/* 2 */
}
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
/* 1 */
  font: inherit;
/* 2 */
}
details {
  display: block;
}
summary {
  display: list-item;
}
template {
  display: none;
}
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
  word-break: normal;
}
.skip-link:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0,0,0,0.6);
  clip: auto !important;
  clip-path: none;
  color: #21759b;
  display: block;
  font-size: 0.875rem;
  font-weight: 700;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}
#content[tabindex="-1"]:focus {
  outline: 0;
}
#wpadminbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0,0,0,0.5);
}
@media (prefers-reduced-motion) {
  * {
    transition-delay: 0s !important;
    transition-duration: 0s !important;
  }
}
.site {
  min-height: 100vh;
}
.admin-bar .site-header {
  top: 58px;
}
.site-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  padding: 1rem 2rem;
  display: flex;
}
.site-branding {
  margin-right: auto;
}
.header-navigation {
  display: flex;
  gap: 2rem;
}
.primary-navigation .menu-wrapper,
.social-navigation .menu-wrapper {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  gap: 1rem;
}
.hero {
  min-height: 100vh;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  position: relative;
}
.hero-picture {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  overflow: hidden;
}
.hero-picture img {
  min-width: 100%;
  min-height: 100%;
  vertical-align: top;
  object-fit: cover;
}
.hero-title {
  padding: 1rem 2rem;
}
.hero-title h1 {
  font: 400 var(--font-size-hero-title)/1 var(--font-hero), var(--font-secondary);
  margin: 0;
  position: relative;
}
.hero-title h1::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0.25em;
  right: 0.25em;
  bottom: 0;
  z-index: -1;
}
.hero-title p {
  margin: 0;
  display: inline-block;
  padding: 0 1rem 0.5rem;
  font-size: var(--font-size-xl);
  line-height: 1;
}
.hero-color-white h1,
.hero-color-white p {
  color: var(--color-white);
}
.hero-color-white h1 {
  text-shadow: 0 0 0.15em rgba(0,0,0,0.8), 0 0 5px rgba(0,0,0,0.5);
}
.hero-color-white h1::after,
.hero-color-white p {
  background: rgba(0,0,0,0.92);
}
.hero-color-black h1,
.hero-color-black p {
  color: #010101;
}
.hero-color-black h1 {
  text-shadow: 0 0 30px rgba(255,255,255,0.5);
}
.hero-color-black h1::after,
.hero-color-black p {
  background: rgba(255,255,255,0.92);
}
.pictures-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
.picture {
  position: relative;
}
.picture img {
  vertical-align: top;
  width: 100%;
  height: auto;
}
.picture:nth-child(odd) .picture-title {
  text-align: right;
}
.picture-description {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 1rem;
}
.picture-title {
  margin: 0;
  font-weight: 200;
  line-height: 1;
}
